/* Services page styles */
.services-page {
    max-width: 1200px;
    margin: 0 auto;
    padding: 30px;
}

.contact-info {
    background: #f5f9f5;
    border-radius: 12px;
    padding: 30px;
    margin: 30px 0;
    box-shadow: 0 2px 8px rgba(44, 85, 48, 0.1);
}

.contact-grid {
    display: flex;
    justify-content: center;
    gap: 40px;
    flex-wrap: wrap;
    margin-top: 20px;
}

.contact-grid p {
    margin: 0;
    font-size: 1.1rem;
    color: #2c5530;
}

.contact-grid strong {
    color: #2c5530;
    margin-right: 8px;
}

.service-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 30px;
    margin-top: 30px;
}

.services {
    margin-top: 40px;
}

main {
    padding: 30px;
}

h1 {
    text-align: center;
    color: #2c5530;
    margin-bottom: 40px;
    font-size: 2.5rem;
}

.service-card {
    background: #e8f3e8;
    border-radius: 12px;
    padding: 32px;
    box-shadow: 0 3px 6px rgba(44, 85, 48, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border: 1px solid #98c9a3;
}

.service-card {
    cursor: pointer;
    position: relative;
}

.service-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(44, 85, 48, 0.15);
    background: #f5f9f5;
}

.service-card h3 {
    color: #2c5530;
    margin-bottom: 16px;
    font-size: 1.8rem;
    font-weight: 600;
}

.service-card p {
    color: #446850;
    line-height: 1.8;
    margin: 0;
    font-size: 1.1rem;
}

.read-more {
    display: inline-block;
    margin-top: 15px;
    color: #7ab587;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.service-card:hover .read-more {
    color: #2c5530;
    transform: translateX(5px);
}

/* Service Detail Pages */
.service-detail {
    background: #ffffff;
    border-radius: 12px;
    padding: 40px;
    margin: 30px auto;
    max-width: 800px;
    box-shadow: 0 2px 8px rgba(44, 85, 48, 0.1);
}

.service-detail section {
    margin-bottom: 40px;
}

.service-detail h2 {
    color: #2c5530;
    font-size: 1.6rem;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 2px solid #e8f3e8;
}

.service-detail p {
    color: #446850;
    line-height: 1.8;
    font-size: 1.1rem;
    margin-bottom: 15px;
}

.service-detail ul, 
.service-detail ol {
    padding-left: 20px;
    margin-bottom: 20px;
}

.service-detail li {
    color: #446850;
    line-height: 1.8;
    font-size: 1.1rem;
    margin-bottom: 10px;
    padding-left: 10px;
}

.service-note {
    background: #f5f9f5;
    padding: 20px;
    border-radius: 8px;
    border-left: 4px solid #7ab587;
}

.service-note p {
    margin: 0;
    font-style: italic;
}

.contact-buttons {
    display: flex;
    gap: 20px;
    margin-top: 30px;
    justify-content: center;
}

.contact-button, .back-button {
    display: inline-block;
    padding: 12px 24px;
    border-radius: 8px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
}

.contact-button {
    background-color: #7ab587;
    color: white;
    border: 2px solid #7ab587;
}

.back-button {
    background-color: transparent;
    color: #7ab587;
    border: 2px solid #7ab587;
}

.contact-button:hover {
    background-color: #2c5530;
    border-color: #2c5530;
    transform: translateY(-2px);
}

.back-button:hover {
    background-color: #f5f9f5;
    transform: translateY(-2px);
}

.service-pricing {
    background-color: #f5f9f5;
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 30px;
}

.service-contact {
    text-align: center;
    margin-top: 40px;
    padding-top: 30px;
    border-top: 2px solid #e8f3e8;
}

.contact-info-preview {
    background: #f5f9f5;
    padding: 20px;
    border-radius: 8px;
    margin: 20px auto;
    max-width: 400px;
}

.contact-info-preview p {
    margin: 10px 0;
    color: #2c5530;
}

.contact-info-preview strong {
    color: #2c5530;
    margin-right: 10px;
}

.service-not-found {
    text-align: center;
    padding: 50px 20px;
}

.service-not-found a {
    display: inline-block;
    margin-top: 20px;
    color: #7ab587;
    text-decoration: none;
    font-weight: 600;
}

.service-not-found a:hover {
    color: #2c5530;
}
